.input {
    height: 30px;
    width: 250px;
    border: none;
    border-bottom: 1px solid whitesmoke;
    background:none;
    color: white;
    padding: 0 10px;
    outline: none;
}

.enter{
    width: 100px;
    height: 30px;
    font-size: 12px;
    background: none;
    border:1px solid whitesmoke;
    color: whitesmoke;
    margin-left: 10px;

}

.enter:hover {
    background: whitesmoke;
    color: #282c34;
    cursor: pointer;
}

.todo__item {
    position: absolute;
    cursor:move;
    min-width: 215px;
    color: black;
    padding: 1em;
    border-radius: 5px;

}

.delete {
    position: absolute;
    right: 5px;
    top: 5px;
    background: none;
    border:none;
}

.delete:hover {
    color: red;
    cursor: pointer;
}